// ContentComponents/Experience.jsx
import React, { useEffect, useState, useLayoutEffect } from 'react';

export const Experience = () => {
  const [animationStage, setAnimationStage] = useState(0); 
  // Stages: 0=initial, 1=titles visible, 2=dates typing, 3=dates complete, 4=descriptions visible, 5=all content static
  const [typingProgress, setTypingProgress] = useState(0); // 0-100 represents percentage of completion
  const [windowWidth, setWindowWidth] = useState(typeof window !== 'undefined' ? window.innerWidth : 1024);

  // Handle responsive sizing
  useLayoutEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Experience data
  const experiences = [
    {
      companyTitle: "Safe Software Inc.",
      positions: [
        {
          title: "Technical Support Specialist – FME Form, Product Team",
          date: "July 2024 – Present",
          bulletPoints: [
            "Specialized in providing technical support for ArcGIS formats and SharePoint integrations.",
            "Created known issue articles, updated knowledge base and troubleshooting articles.",
            "Participated in and delivered webinars and Q&A sessions."
          ]
        },
        {
          title: "Technical Support Intern – Practicum: Data Integration for Utilities with FME",
          date: "April 2024 – May 2024",
          bulletPoints: [
            "Created tutorial articles on FME and ArcGIS Utility Network.",
            "Topics included reading/writing ArcGIS Feature Service, database operations/change detection, generating HTML reports, and synchronizing datasets."
          ]
        }
      ]
    },
    {
      companyTitle: "The University of British Columbia – Faculty of Forestry",
      positions: [
        {
          title: "Course Marker – CONS 340 Introduction to GIS for Forestry and Conservation",
          date: "January 2023 – May 2023",
          bulletPoints: [
            "Appraised students' assignments and assigned grades.",
            "Provided timely and detailed feedback on assignments.",
            "Coordinated and communicated with the course instructor to maintain consistency in grading."
          ]
        }
      ]
    },
    {
      companyTitle: "Integrated Remote Sensing Studio – UBC Forestry",
      positions: [
        {
          title: "Work Learn Project Assistant",
          date: "May 2022 – September 2022",
          bulletPoints: [
            "Assisted research associates and graduate students with their projects, specifically identifying code issues for R packages, rectifying data, and collecting field data.",
            "Utilized RStudio to identify code issues for an R package developed by a research associate.",
            "Rectified delineations of tree canopy from provenance tests using ArcGIS Pro for a graduate student's project.",
            "Contributed to fieldwork by collecting tree inventory data using field surveying tools, such as Haglof PosTex and hypsometer, Trimble GNSS, and GeoSLAM, in support of a graduate student.",
            "Maintained effective communication with lab members and supervisor, providing regular updates on project progress and identifying potential issues and solutions."
          ]
        }
      ]
    }
  ];
  
  // Animation sequence with cleanup for timers
  useEffect(() => {
    const timeoutIds = [];
    const intervalIds = [];
    
    // Start the sequence
    const t1 = setTimeout(() => {
      setAnimationStage(1); // Titles visible
      
      const t2 = setTimeout(() => {
        setAnimationStage(2); // Dates typing
        
        const typingDuration = 2000; // time to type in ms
        const intervalTime = 30; // update frequency in ms
        const steps = typingDuration / intervalTime;
        let currentStep = 0;
        
        const typingInterval = setInterval(() => {
          currentStep++;
          const progress = Math.min((currentStep / steps) * 100, 100);
          setTypingProgress(progress);
          
          if (progress >= 100) {
            clearInterval(typingInterval);
            setAnimationStage(3); // Dates complete
            
            const t3 = setTimeout(() => {
              setAnimationStage(4); // Descriptions visible
              
              const t4 = setTimeout(() => {
                setAnimationStage(5); // All content static
              }, 1200);
              timeoutIds.push(t4);
            }, 400);
            timeoutIds.push(t3);
          }
        }, intervalTime);
        intervalIds.push(typingInterval);
      }, 600);
      timeoutIds.push(t2);
    }, 400);
    timeoutIds.push(t1);
    
    // Cleanup
    return () => {
      timeoutIds.forEach(id => clearTimeout(id));
      intervalIds.forEach(id => clearInterval(id));
    };
  }, []);
  
  // Returns typed text based on current progress (for plain text)
  const getTypedText = (text) => {
    if (animationStage < 2) return '';
    if (animationStage >= 3) return text; // fully typed
    
    const charsToShow = Math.floor((typingProgress / 100) * text.length);
    return text.substring(0, charsToShow);
  };

  // Returns either the typed plain text (stages 2 -> partial) or full HTML (stage >= 3)
  const getDateContent = (date) => {
    if (animationStage < 2) {
      // Not typing yet
      return '';
    } else if (animationStage < 3) {
      // Typing in progress: strip out any HTML tags to avoid showing raw <a>
      const stripped = date.replace(/<[^>]+>/g, '');
      return getTypedText(stripped);
    } else {
      // Typing done (stage >= 3): show full HTML with link
      return (
        <span
          dangerouslySetInnerHTML={{
            __html: date.replace(/<a /g, '<a class="text-green-500" '),
          }}
        />
      );
    }
  };

  // Define styles dynamically (no media queries in inline styles)
  const experienceItemStyle = {
    marginBottom: 0,
    paddingBottom: 0
  };

  const companyTitleStyle = {
    fontSize: windowWidth < 768 ? '1.5rem' : '1.75rem',
    fontWeight: 700,
    marginBottom: '0.25rem',
    paddingBottom: 0,
    opacity: animationStage >= 1 ? 1 : 0,
    transition: 'opacity 0.6s ease-in-out'
  };

  const positionTitleStyle = {
    fontSize: windowWidth < 768 ? '1rem' : '1.125rem',
    fontWeight: 600,
    marginTop: '0.1rem',
    marginBottom: 0,
    paddingBottom: 0,
    opacity: animationStage >= 1 ? 1 : 0,
    transition: 'opacity 0.6s ease-in-out'
  };

  const dateContainerStyle = {
    color: '#9ca3af',
    marginTop: 0,
    marginBottom: 0,
    paddingTop: 0,
    paddingBottom: 0,
    lineHeight: 1.2,
    height: windowWidth < 768 ? '1.3rem' : '1.5rem',
    fontSize: windowWidth < 768 ? '0.75rem' : '0.875rem'
  };

  const dateTextStyle = {
    margin: 0,
    padding: 0
  };

  const bulletListStyle = {
    listStyleType: 'disc',
    paddingLeft: '1.25rem',
    marginTop: '0.25rem',
    marginBottom: animationStage >= 4 ? '0.75rem' : '0',
    maxHeight: animationStage >= 4 ? '1000px' : '0',
    overflow: 'hidden',
    opacity: animationStage >= 4 ? 1 : 0,
    transform: animationStage >= 4 ? 'scaleY(1)' : 'scaleY(0)',
    transformOrigin: 'top',
    transition: 'opacity 1s cubic-bezier(0.25, 0.1, 0.25, 1), transform 1s cubic-bezier(0.25, 0.1, 0.25, 1), margin-bottom 1s cubic-bezier(0.25, 0.1, 0.25, 1), max-height 1s cubic-bezier(0.25, 0.1, 0.25, 1)',
  };

  const bulletPointStyle = {
    fontSize: windowWidth < 768 ? '0.875rem' : '1rem',
    lineHeight: '1.3',
    marginBottom: '0.25rem'
  };
  
  const projectTitleStyle = {
    fontWeight: 500,
    marginTop: '0.75rem',
    marginBottom: '0.25rem',
    opacity: animationStage >= 4 ? 1 : 0,
    transform: animationStage >= 4 ? 'translateY(0)' : 'translateY(-5px)',
    transition: 'opacity 0.6s ease-in-out, transform 0.6s ease-in-out',
  };

  return (
    <div className="py-4">
      <p className="mb-4 text-gray-500 italic">A detailed resume with additional information can be found <a href="/resume.pdf" className="text-green-500 hover:underline">here</a>.</p>
      <div>
        {experiences.map((company, companyIndex) => (
          <div key={companyIndex} className="mb-4">
            {/* Company name */}
            <h3 style={companyTitleStyle}>
              {company.companyTitle}
            </h3>
            
            {/* Positions at this company */}
            {company.positions.map((position, posIndex) => (
              <div key={`${companyIndex}-${posIndex}`} style={experienceItemStyle} className="mb-2">
                {/* Position title and date */}
                <div>
                  <h4 style={positionTitleStyle}>
                    {position.title}
                  </h4>
                  <div style={dateContainerStyle}>
                    {/* Use getDateContent to show typed text or full HTML */}
                    <p style={dateTextStyle}>
                      {getDateContent(position.date)}
                    </p>
                  </div>
                </div>

                {/* Regular bullet points */}
                {position.bulletPoints && (
                  <ul style={bulletListStyle}>
                    {position.bulletPoints.map((point, bulletIndex) => (
                      <li key={`${companyIndex}-${posIndex}-${bulletIndex}`} style={bulletPointStyle}>
                        <span dangerouslySetInnerHTML={{ 
                          __html: point.replace(/<a /g, '<a class="text-green-500" ') 
                        }} />
                      </li>
                    ))}
                  </ul>
                )}
                
                {/* Special handling for projects with individual and group sections */}
                {position.individualProject && (
                  <div style={{
                    opacity: animationStage >= 4 ? 1 : 0,
                    maxHeight: animationStage >= 4 ? '1000px' : '0',
                    overflow: 'hidden',
                    transition: 'opacity 1s ease-in-out, max-height 1s ease-in-out',
                  }}>
                    <p style={projectTitleStyle}>{position.individualProject.title}</p>
                    <ul style={{...bulletListStyle, marginTop: 0, opacity: 1, transform: 'scaleY(1)'}}>
                      {position.individualProject.bulletPoints.map((point, bulletIndex) => (
                        <li key={`ind-${companyIndex}-${posIndex}-${bulletIndex}`} style={bulletPointStyle}>
                          <span dangerouslySetInnerHTML={{ 
                            __html: point.replace(/<a /g, '<a class="text-green-500" ') 
                          }} />
                        </li>
                      ))}
                    </ul>
                    
                    <p style={projectTitleStyle}>{position.groupProject.title}</p>
                    <ul style={{...bulletListStyle, marginTop: 0, opacity: 1, transform: 'scaleY(1)'}}>
                      {position.groupProject.bulletPoints.map((point, bulletIndex) => (
                        <li key={`grp-${companyIndex}-${posIndex}-${bulletIndex}`} style={bulletPointStyle}>
                          <span dangerouslySetInnerHTML={{ 
                            __html: point.replace(/<a /g, '<a class="text-green-500" ') 
                          }} />
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            ))}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Experience;