import React from 'react'
import ReactDOM from 'react-dom/client'
import App from './App.jsx'
import './index.css'

// Add some debugging to help identify issues
console.log('main.jsx is executing');
console.log('React version:', React.version);
console.log('Environment:', import.meta.env.MODE);

// Wrap the rendering in a try/catch to log any errors
try {
  ReactDOM.createRoot(document.getElementById('root')).render(
    <React.StrictMode>
      <App />
    </React.StrictMode>,
  );
  console.log('React app mounted successfully');
} catch (error) {
  console.error('Error rendering React app:', error);
  // Display error on page
  const errorDiv = document.createElement('div');
  errorDiv.style.cssText = 'position:fixed;top:50%;left:50%;transform:translate(-50%,-50%);background:red;color:white;padding:20px;z-index:9999;';
  errorDiv.innerHTML = `<h2>React Rendering Error</h2><pre>${error.message}\n\n${error.stack}</pre>`;
  document.body.appendChild(errorDiv);
}