import React, { useState, useEffect } from 'react';

function GlobeIcon() {
  const [isFocused, setIsFocused] = useState(true);

  // Handle focus and blur to pause/resume animation without resetting
  useEffect(() => {
    function handleWindowFocus() {
      setIsFocused(true);
    }
    function handleWindowBlur() {
      setIsFocused(false);
    }

    window.addEventListener("focus", handleWindowFocus);
    window.addEventListener("blur", handleWindowBlur);

    return () => {
      window.removeEventListener("focus", handleWindowFocus);
      window.removeEventListener("blur", handleWindowBlur);
    };
  }, []);

  return (
    <div className="w-12 h-12 flex items-center justify-center">
      <svg
        className="animate-globe-spin w-full h-full"
        style={{ animationPlayState: isFocused ? "running" : "paused" }}
        viewBox="0 0 64 64"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        {/* 
          Clipping path to ensure lines and continents 
          do not extend past the circle boundary
        */}
        <defs>
          <clipPath id="circleClip">
            <circle cx="32" cy="32" r="30.5" />
          </clipPath>
        </defs>

        {/* Outer Circle */}
        <circle
          cx="32"
          cy="32"
          r="30.5"
          stroke="#CCC"
          strokeWidth="2"
          fill="#90CDF4"
        />

        {/* Group all lines and continents so they don't extend past the circle */}
        <g clipPath="url(#circleClip)">
          {/* Latitude Lines */}
          <path d="M9 12C12 14 52 14 55 12" stroke="#2F855A" strokeWidth="4" />
          <path d="M3.2 22C12 24 52 24 60.8 22" stroke="#2F855A" strokeWidth="4" />
          <path d="M1.5 32C12 34 52 34 62.5 32" stroke="#2F855A" strokeWidth="4" />
          <path d="M3.2 42C12 44 52 44 60.8 42" stroke="#2F855A" strokeWidth="4" />
          <path d="M9 52C12 54 52 54 55 52" stroke="#2F855A" strokeWidth="4" />

          {/* Longitude Lines */}
          <path d="M32 1.5V62.5" stroke="#2F855A" strokeWidth="4" />
          <path d="M44 60C50 44 50 20 44 4" stroke="#2F855A" strokeWidth="4" />
          <path d="M20 60C14 44 14 20 20 4" stroke="#2F855A" strokeWidth="4" />
        </g>
      </svg>
    </div>
  );
}

export const GlobeIconSVG = () => (
  <svg viewBox="0 0 64 64" fill="none" xmlns="http://www.w3.org/2000/svg">
    <defs>
      <clipPath id="circleClip">
        <circle cx="32" cy="32" r="30.5" />
      </clipPath>
    </defs>
    <circle cx="32" cy="32" r="30.5" stroke="#CCC" strokeWidth="2" fill="#90CDF4" />
    <g clipPath="url(#circleClip)">
      <path d="M9 12C12 14 52 14 55 12" stroke="#2F855A" strokeWidth="4" />
      <path d="M3.2 22C12 24 52 24 60.8 22" stroke="#2F855A" strokeWidth="4" />
      <path d="M1.5 32C12 34 52 34 62.5 32" stroke="#2F855A" strokeWidth="4" />
      <path d="M3.2 42C12 44 52 44 60.8 42" stroke="#2F855A" strokeWidth="4" />
      <path d="M9 52C12 54 52 54 55 52" stroke="#2F855A" strokeWidth="4" />
      <path d="M32 1.5V62.5" stroke="#2F855A" strokeWidth="4" />
      <path d="M44 60C50 44 50 20 44 4" stroke="#2F855A" strokeWidth="4" />
      <path d="M20 60C14 44 14 20 20 4" stroke="#2F855A" strokeWidth="4" />
    </g>
  </svg>
);

export default GlobeIcon;